import React, { useState } from "react";
// import { useHistory } from "react-router-dom";
import { MyGoogleLogin } from "./GoogleLogin";
import "./Login.css";

function Login({ setLogin }) {
  // const history = useHistory();
  const [loginError, setLoginError] = useState("");
  const loginFunc = (r) => {
    console.log(r)
    localStorage.setItem("asm_token", r.accessToken);
    localStorage.setItem("asm_token_email", r.profileObj.email);
    setLogin(true);
  };
  const logoutFunc = () => {
    setLogin(false);
    localStorage.removeItem("asm_token");
    localStorage.removeItem("asm_token_email");
    
  };
  return (
    <div className="p_login">
      <div className="log_col1">
        <img
          src={window.location.origin + "/images/Login_screen.svg"}
          alt="login"
          className="login_bg"
        />
      </div>
      <div className="log_col2">
        <h1 className="log_name">Area Sales Manager</h1>
        <div
          className="login_button"
        >
          <MyGoogleLogin loginFunc={loginFunc} logoutFunc={logoutFunc} setLoginError={setLoginError} setLogin={setLogin} />

          {/* <img
            src={window.location.origin + "/images/LOGIN BUTTON.svg"}
            alt="login"
            className="login_but"
          />

          <p className="log_p1">login with google</p> */}
        </div>
        <div style={{ marginTop: "10px" }}>{loginError}</div>
      </div>
    </div>
  );
}

export default Login;
