import React from "react";
import DataTable, { createTheme } from "react-data-table-component";
import { table_data } from "./TableData";
import "./Table.css";
// import StatusBar from "../StatusBar/StatusBar";
import TripleDotDropDown from "../TripleDotDropDown";

import DataTableExtensions from "react-data-table-component-extensions";
createTheme("solarized", {
  text: {
    primary: "black",
    secondary: "#b2b2b2",
  },
  background: {
    default: "#f2f2f2",
  },
  boxshadow: {
    default: "5px 5px 30px #DEDEDEBF",
  },
  context: {
    background: "#cb4b16",
    text: "#FFFFFF",
  },
  divider: {
    default: "#fafafa",
  },
  action: {
    button: "rgba(0,0,0,.54)",
    hover: "rgba(0,0,0,.08)",
    disabled: "rgba(0,0,0,.12)",
  },
});

function Table({ api }) {
  const columns = [
    {
      name: "Lead ID",
      sortable: true,
      center: true,
      cell: (d) => <div>{d.lead_id}</div>,
    },
    {
      name: "Date",
      sortable: true,
      center: true,
      cell: (d) => <div>{d.lead_date}</div>,
    },
    {
      name: "Time",
      sortable: true,
      center: true,
      cell: (d) => <div>{d.lead_time}</div>,
    },
    // {
    //   name: "FSE Id",
    //   cell: (d) => <div>{d.user_id}</div>,
    //   sortable: true,
    //   center: true,
    // },
    {
      name: "Merchant name",
      cell: (d) => <div>{d.merchant_name}</div>,
      sortable: true,
      center: true,
    },
    {
      name: "Store name",
      cell: (d) => <div>{d.shop_name}</div>,
      sortable: true,
      center: true,
      // cell: d => <div>{<StatusBar o1={d.ride1_date} o2={d.ride2_date} o3={d.ride3_date} o4={d.ride4_date} o5={d.ride5_date}/>}</div>
    },
    {
      name: "City",
      cell: (d) => <div>{d.city}</div>,
      sortable: true,
      center: true,
    },
    {
      name: "Status",
      cell: (d) => <div style={d.qc_status==="Approved"? {color: "#36A000"}:{color:"#FF3B3B"}}>{d.qc_status}</div>,
      sortable: true,
      center: true,
    },
    {
      name: "QC Remarks",
      cell: (d) => <div>{d.qc_remark}</div>,
      sortable: true,
      center: true,
    },
  ];
  const tableData = {
    columns,
    data: api,
  };
  return (
    <div className="table_data">
      <div className="card_table">
        <div className="menu-container">
          <TripleDotDropDown />
        </div>
        <DataTableExtensions
          {...tableData}
          export={false}
          filterPlaceholder={`Search`}
        >
          <DataTable
            columns={columns}
            data={table_data.data}
            // defaultSortFieldId={2}
            pagination
            theme="solarized"
          />
        </DataTableExtensions>
      </div>
    </div>
  );
}

export default Table;
