import React, { useState, useEffect } from "react";
import "./Dashboard.css";
import { Link } from "react-router-dom";
import axios from "../config/axios";
// import Chart from "react-apexcharts";
// import { apexRadial } from "../ApexOptions";

function Dashboard({ setLogin, setTlUserId }) {
  // const [active, setActive] = useState(true);
  const [active1, setActive1] = useState(true);
  const [active7, setActive7] = useState(true);
  const [tlLists, setTlLists] = useState([]);
  const [asmDetail, setasmDetail] = useState({});
  const [asmData, setAsmData] = useState({});

  

  const handleClick27 = () => {
    setActive7(!active7);
    const location_click = document.getElementById("location_card");

    if (location_click.style.display === "none") {
      location_click.style.display = "block";
    } else {
      location_click.style.display = "none";
    }
  };

  const handleClick3 = (id) => {
    setTlUserId(id.tl_id);
    console.log(id, "id");
  };

  // const handleClick = () => {
  //   setActive(!active);
  // };
  const changeImage = (e) => {
    e.target.src=`${window.location.origin + "/images/profile_image.svg"}`
  }

  const handleClick2 = () => {
    const data1 = {
      asm_id: asmDetail.asm_id, 
      type: "1",
    };
    const data = {
      asm_id: asmDetail.asm_id,
      type: "0",
    };
    setActive1(!active1);
    const getData = axios.post("/tl_list", active1 ? data : data1)
      .then((response) => {
        setTlLists(response.data.tl);
      });
      return getData
  };
  const handleClick1 = () => {
    const dash_drop_click = document.getElementById("logout_drop");

    if (dash_drop_click.style.display === "none") {
      dash_drop_click.style.display = "block";
    } else {
      dash_drop_click.style.display = "none";
    }
  };

  useEffect(() => {
  
    const data1 = {
      "email_id" : localStorage.getItem("asm_token_email")
    };

    const getAsmData = axios.post("/check_email",data1)

    .then((resp)=> {
      setasmDetail(resp.data.asm[0])
      console.log(resp.data.asm[0],"check-email")
      console.log(resp.data.asm[0].asm_id,"check_id")
      const data = {
        asm_id: resp.data.asm[0].asm_id,
        type: "1",
      };
      
      
    const getData = axios.post("/tl_list", data).then((response) => {
      setTlLists(response.data.tl);
      console.log(response.data.tl,"tl-data");
    });
    console.log(resp.data.asm[0].asm_id,"asm_id")
    const data2 = {
      "asm_id": resp.data.asm[0].asm_id
    }
      const getTotal = axios.post("/asm_stats",data2)
      .then((res)=>{
      console.log(res.data,"count");
      setAsmData(res.data)

    })
    return getTotal
  });

  return getAsmData
  }, []);

  console.log(tlLists);

  return (
    <div className="dashboard">
      <div className="dash_row1">
        <div className="dash_width">
          <div className="profile_box">
            {/* <div className="pro_col3">
              <img
                src={window.location.origin + "/images/arrow back.svg"}
                alt="profile"
                className="back_but"
              />
              <p className="back">back</p>
            </div> */}
          </div>
          <div className="dash1_head">
            <h1 className="dash1_head_name">Area Sales Manager</h1>
          </div>
          {/* logout function dropdpwn*/}
          <div
            className="logout_drop"
            id="logout_drop"
            style={{ display: "none" }}
            onClick={() => {
              localStorage.removeItem("asm_token");
              localStorage.removeItem("asm_token_email");
              setLogin(false);
            }}
          >
            Logout
          </div>
          <div className="pro_col1" onClick={() => handleClick1()}>
            <img
              src={asmDetail.profile_image}
              alt="profile"
              className="dash_profile"
              onError = {(e)=> changeImage(e)}
            />

            <div className="pro_col2">
              <h1 className="pro_name">{asmDetail.full_name}</h1>
              <p className="d1_p1">ASM</p>
            </div>
          </div>
        </div>
      </div>
      <div className="dash_row2">
        <div className="dash_col1">
          <div
            className="dash_box1"
            style={{
              backgroundImage: `url(${
                window.location.origin + "/images/total_leads.svg"
              })`,
            }}
          >
            <p className="dash_p1">Leads</p>
            <p className="dash_p2">{asmData.total_leads}</p>
            {/* <p className="dash_p3">+200</p> */}
          </div>
          <div
            className="dash_box1"
            style={{
              backgroundImage: `url(${
                window.location.origin + "/images/qc_conversion.svg"
              })`,
            }}
          >
            <p className="dash_p1">QC Conversion</p>
            <p className="dash_p2">{asmData.qc_conversion}</p>
            {/* <p className="dash_p3">+100</p> */}
          </div>
          <div
            className="dash_box1"
            style={{
              backgroundImage: `url(${
                window.location.origin + "/images/earned_points.svg"
              })`,
            }}
          >
            <p className="dash_p1">Earned Points</p>
            <p className="dash_p2">{asmData.total_points}</p>
            {/* <p className="dash_p3">+800</p> */}
          </div>
          <div
            className="dash_box1"
            style={{
              backgroundImage: `url(${
                window.location.origin + "/images/total_leads.svg"
              })`,
            }}
          >
            <p className="dash_p1">Active Fse</p>
            <p className="dash_p2">{asmData.total_active_fse}</p>
            {/* <p className="dash_p3">+200</p> */}
          </div>
          {/* <div
            className="dash_box1"
            style={{
              backgroundImage: `url(${
                window.location.origin + "/images/meeting2.svg"
              })`,
            }}
          >
            <p className="dash_p4">Meetings</p>
            <div className="content_row1">
              <div className="content_col1">
                <p className="d1_p3">GR Meet</p>
                <p className="d1_p2">03</p>
              </div>
              <div className="content_col1">
                <p className="d1_p3">WR Meet</p>
                <p className="d1_p2">17</p>
              </div>
            </div>
          </div> */}
          {/* <div
            className="dash_box1"
            style={{
              backgroundImage: `url(${
                window.location.origin + "/images/todays_active_attendence.svg"
              })`,
            }}
          >
            <p className="dash_p4">Today’s Active Attendance</p>
            <div className="content_row1">
              <div className="content_col2">
                <p className="d1_p4">TL</p>
                <p className="d1_p5">10</p>
              </div>
              <div className="content_col2">
                <p className="d1_p4">FSE</p>
                <p className="d1_p5">80</p>
              </div>
            </div>
          </div> */}


        </div>
        {/* <div className="dash_col2">
          <div className="dash_card">
            <div className="dash_card_row1">
              <Chart
                options={apexRadial.options}
                series={apexRadial.series}
                type="radialBar"
                height={220}
              />
            </div>
            <div className="dash_card_row2">
              <p className="dash_card_p1">Today’s FSE Attendance</p>
              <div className="dash_card_container">
                <div className="dash_box4">
                  <p className="dash_card_p2">Present</p>
                  <p className="dash_card_p3">17</p>
                </div>
                <div className="dash_box2">
                  <p className="dash_card_p2">Absent</p>
                  <p className="dash_card_p3">03</p>
                </div>
                <div className="dash_box3">
                  <p className="dash_card_p2">Today’s Attendance</p>
                  <p className="dash_card_p3">70%</p>
                </div>
              </div>
            </div>
            <div className="dash_card_row2">
              <p className="dash_card_p1">Today’s TL Attendance</p>
              <div className="dash_card_container">
                <div className="dash_box4">
                  <p className="dash_card_p2">Present</p>
                  <p className="dash_card_p3">17</p>
                </div>
                <div className="dash_box2">
                  <p className="dash_card_p2">Absent</p>
                  <p className="dash_card_p3">03</p>
                </div>
                <div className="dash_box3">
                  <p className="dash_card_p2">Today’s Attendance</p>
                  <p className="dash_card_p3">70%</p>
                </div>
              </div>
            </div>
          </div>
        </div> */}
      </div>
      <div className="dash_row3">
        <div className="dash_card2">
          <div className="dash_card2_row">
            <p className="dash_card2_p1">Team leads(TL) List</p>
            <div className="d1_col8" onClick={() => handleClick27()}>
              <p className="d1_p7">{active1 ? "Today" : "This month"}</p>
              {active7 ? (
                <img
                  src={window.location.origin + "/images/arrow_drop_UP.svg"}
                  alt="drop down"
                  className="drop_down"
                />
              ) : (
                <img
                  src={window.location.origin + "/images/arrow_drop_down.svg"}
                  alt="drop down"
                  className="drop_down"
                />
              )}
              <div
                className="location_card"
                id="location_card"
                style={{ display: "none" }}
              >
                <p className="d1_p6" onClick={() => handleClick2()}>
                  {!active1 ? "Today" : "This month"}
                </p>
              </div>
            </div>
          </div>

          {tlLists.map((tl_data) => (
            <div className="dash_card3" key={tl_data.tl_id}>
              <Link
                to="/dashboard_tl"
                style={{
                  display: "flex",
                  width: "-webkit-fill-available",
                  justifyContent: "space-between",
                }}
                id={tl_data}
                onClick={(id) => handleClick3(tl_data)}
              >
                <div className="dash_card3_col0">
                  <img
                    src={tl_data.profile_image}
                    alt="profile"
                    className="dash_profile"
                    onError = {(e)=> changeImage(e)}
                  />
                  {/* (src={window.location.origin + "/images/profile_image.svg"}) */}
                </div>
                <div className="dash_card3_col1">
                  <p className="dash_card3_p2">{tl_data.full_name}</p>
                  <p className="dash_card3_p3">{tl_data.project_name}</p>
                </div>

                <div className="dash_card3_col1">
                  <p className="dash_card3_p4">{tl_data.location}</p>
                  <p className="dash_card3_p3">Location</p>
                </div>
                <div className="dash_card3_col1">
                  <p className="dash_card3_p5">{tl_data.head_count}</p>
                  <p className="dash_card3_p3">FSE Count</p>
                </div>
                <div className="dash_card3_col1">
                  <p className="dash_card3_p8">{tl_data.total_leads}</p>
                  <p className="dash_card3_p3">Leads</p>
                </div>
                <div className="dash_card3_col1">
                  <p className="dash_card3_p9">{tl_data.total_qc_passed}</p>
                  <p className="dash_card3_p3">QC Conversion</p>
                </div>
                {/* <div className="dash_card3_col1">
                  <p className="dash_card3_p10">60%</p>
                  <p className="dash_card3_p3">Attendance</p>
                </div> */}
                <div className="dash_card3_col1">
                  <p className="dash_card3_p10" style={{ color: "#AA6CFF" }}>
                    {tl_data.total_points_earned}
                  </p>
                  <p className="dash_card3_p3">Achieved points</p>
                </div>
              </Link>

              {/* <div className="dash_card3_col1">
                <div class="custom-control custom-switch">
                  <input
                    type="checkbox"
                    class="custom-control-input"
                    id="darkSwitch"
                    onClick={() => handleClick()}
                    checked={active}
                  />
                  <label class="custom-control-label" for="darkSwitch"></label>
                </div>
                {active ? (
                  <p className="dash_card3_p7" style={{ color: "#2FBC60" }}>
                    Mark as not Active
                  </p>
                ) : (
                  <p className="dash_card3_p7" style={{ color: "red" }}>
                    Not Active
                  </p>
                )}
              </div> */}
              <Link
                to="/dashboard_tl"
                id={tl_data}
                onClick={(id) => handleClick3(tl_data)}
              >
                <div className="dash_card3_col2">
                  <img
                    src={window.location.origin + "/images/front_arrow.svg"}
                    alt="profile"
                    className="front_arrow"
                  />
                </div>
              </Link>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
}

export default Dashboard;
